import React from "react";
import { Grid, Stack } from "@material-ui/core";
import { styled } from "@material-ui/styles";
import SimpleBar from "simplebar-react";
import { SingleGoalType } from "../../../../../utils/typeHelpers";
import { useWindowDimensions } from "../../../../../utils/helperFunction";
import PostViewTopInfo from "../../../../../components/PostViewTopInfo";
import PostViewBottomInfo from "../../../../../components/PostViewBottomInfo";

const LeftContainer = ({
  data,
  cngRefreshState,
}: {
  data: SingleGoalType;
  cngRefreshState: any;
}) => {
  const { height } = useWindowDimensions();
  // console.log('$$$: ',data);
  return (
    <Grid item xs={false} sm={false} md={4}>
      <SimpleBar
        style={{ maxHeight: height - 60 }}
        forceVisible="y"
        autoHide={false}
      >
        <Stack
          direction="column"
          justifyContent="center"
          alignItems="center"
          spacing={1}
          sx={{ padding: 2 }}
        >
          <PostViewTopInfo
            profileID={data.profile}
            image={data.profile_image}
            userName={data.profile_username}
            endorser={data.ngo_username}
          />
          <GoalImage src={data.image} alt="GoalImg" />
          <PostViewBottomInfo
            slug={data.slug}
            goalID={data.id}
            isLiked={data?.is_liked}
            isSaved={data?.is_saved}
            title={data.title}
            total_amount={data.total_amount}
            paid_amount={data.paid_amount}
            supporters={data.goal_payment}
            total_donor={data.donor_count}
            goal_likes={data.goal_likes}
            comments={data.goal_comment}
            total_like_count={data.total_like_count}
            total_comment_count={data.total_comment_count}
            handleNewComment={cngRefreshState}
          />
        </Stack>
      </SimpleBar>
    </Grid>
  );
};

export default LeftContainer;

const GoalImage = styled("img")(() => ({
  height: 150,
  width: 150,
  borderRadius: 10,
}));
