import { Button, Stack, Container, Grid, TextField } from "@material-ui/core";
import React, { useState, useEffect, SyntheticEvent, useRef } from "react";

import { Field, FieldProps, Form, Formik } from "formik";

import {
  AddFunction,
  CreateGoalResponse,
  CreateGoalType,
  RootAppState,
  SingleGoalType,
  EditGoalType,
} from "../../../../utils/typeHelpers";

import {
  useMutation,
  UseMutationResult,
  useQuery,
  UseQueryResult,
} from "react-query";
import { API_URL, CREATE_GOAL_API } from "../../../../utils/Api";
import { useSelector } from "react-redux";
import { useLocation, useNavigate } from "react-router";

import Loader from "../../../../components/Loader";
import { MessageError } from "../../../../components/AlertMessageError";
import { MessageSuccess } from "../../../../components/AlertMessageSuccess";
import useAxios from "../../../../utils/useAxios";
import LeftContainer from "./Components/LeftContainer";
import ImageInput from "./Components/ImageInput";
import { validationSchemaEditGoal } from "./utils/schemaEditGoal";
import { fields, initialValueEdit } from "./utils/data";
import VideoInput from "./Components/VideoInput";

const EditGoal = () => {
  const location = useLocation();
  const slug = location.state;
  const refError = useRef<null | AddFunction>(null);
  const refSuccess = useRef<null | AddFunction>(null);

  const navigate = useNavigate();
  const accesstoken = useSelector(
    (state: RootAppState) => state.AuthReducer.userInfo.access
  );

  const axiosInstance = useAxios();

  const querySingleGoal: UseQueryResult<SingleGoalType, Error> = useQuery(
    ["getSingleGoal", slug],
    async () => {
      const response = await axiosInstance.get(`/retrieve-goal/${slug}`);

      return response.data;
    }
  );

  const {
    data: dataGoal,
    isError: dataisError,
    isLoading: dataisLoading,
  } = querySingleGoal;

  //api mutation

  const mutationEditGoal: UseMutationResult<EditGoalType, Error, EditGoalType> =
    useMutation(["EditGoalTypeMutation"], async (data) => {
      var formdata = new FormData();
      if (data.image !== null) {
        formdata.append("image", data.image ? data.image : "");
      }
      formdata.append("title", `${data.title}`);
      formdata.append("short_description", `${data.short_description}`);
      formdata.append("full_description", `${data.full_description}`);
      formdata.append("buying_item", `${data.buying_item}`);
      formdata.append("online_source_url", `${data.online_source_url}`);
      if (data.media !== null) {
        data.media.map((file) => formdata.append("media", file ? file : ""));
      }
      const response = await axiosInstance.patch(
        `${API_URL}/update-goal/${slug}/`,
        formdata
      );
      return response.data;
    });

  const [initialvalue, setInitialValue] =
    useState<EditGoalType>(initialValueEdit);
  const { isLoading, isError, isSuccess, data } = mutationEditGoal;
  console.log(data, isError, isSuccess);

  const handleCreateError = (msg: string) => {
    refError.current?.(msg);
  };

  const handleCreateSucess = (msg: string) => {
    refSuccess.current?.(msg);
  };

  useEffect(() => {
    let didCancel = false;

    if (isSuccess) {
      if (data !== undefined) {
        if (typeof data.title === "string") {
          handleCreateSucess("Goal Edit Successful");
          setTimeout(() => {
            navigate("../");
          }, 3000);
        } else {
          handleCreateError("Goal Edit Failed");
        }
      }
    }

    return () => {
      didCancel = true;
    };
  }, [isError, isSuccess]);

  useEffect(() => {
    if (dataGoal !== undefined) {
      setInitialValue({
        ...initialvalue,

        title: dataGoal.title,
        short_description: dataGoal.short_description,
        full_description: dataGoal.full_description,
        buying_item: dataGoal.buying_item,
        online_source_url: dataGoal.online_source_url,
      });
    }
  }, [dataGoal]);

  if (isLoading) {
    return <Loader />;
  }
  return (
    <div>
      <MessageError
        children={(add: AddFunction) => {
          refError.current = add;
        }}
      />
      <MessageSuccess
        children={(add: AddFunction) => {
          refSuccess.current = add;
        }}
      />
      <Grid container component="main">
        {dataGoal !== undefined && (
          <LeftContainer data={dataGoal} cngRefreshState={() => {}} />
        )}
        <Grid item xs={12} sm={12} md={8}>
          <Formik
            initialValues={initialvalue}
            validationSchema={validationSchemaEditGoal}
            onSubmit={(values, actions) => {
              alert(JSON.stringify(values, null, 2));
              mutationEditGoal.mutate(values);
              actions.setSubmitting(false);
            }}
            enableReinitialize
          >
            <Form>
              <Container component="main" maxWidth="sm">
                <Stack
                  direction="column"
                  justifyContent="center"
                  alignItems="center"
                  spacing={2}
                  p={5}
                >
                  {fields.map((item) =>
                    item.value === "image" ? (
                      <Field
                        name={item.value}
                        component={ImageInput}
                        label={item.label}
                        type="input"
                      />
                    ) : item.value === "media" ? (
                      <Field
                        name={item.value}
                        component={VideoInput}
                        label={item.label}
                        type="input"
                      />
                    ) : (
                      <Field
                        name={item.value}
                        component={EditTextField}
                        label={item.label}
                        type="input"
                      />
                    )
                  )}
                  <Button type="submit" variant="outlined" color="secondary">
                    EDIT GOAL
                  </Button>
                </Stack>
              </Container>
            </Form>
          </Formik>
        </Grid>
      </Grid>
    </div>
  );
};

export default EditGoal;

interface CustomInputFieldProps extends FieldProps {
  label?: string;
  type?: string;
}
const EditTextField = ({ field, form, label, type }: CustomInputFieldProps) => {
  const renderHelperText = () => {
    if (form.touched[field.name] && form.errors[field.name]) {
      return form.errors[field.name];
    }
  };
  return (
    <TextField
      fullWidth
      variant="outlined"
      {...field}
      label={label}
      placeholder={label}
      multiline={field.name === "full_description" ? true : false}
      rows={field.name === "full_description" ? 4 : 1}
      error={Boolean(form.touched[field.name] && form.errors[field.name])}
      helperText={renderHelperText()}
    />
  );
};
