import React, { useEffect, useState } from "react";
import { useDropzone } from "react-dropzone";
import { useFormikContext, FieldProps } from "formik";

import { styled } from "@material-ui/styles";
import { Icon } from "@iconify/react";
import imageFill from "@iconify/icons-eva/image-fill";
import { Stack, Typography, Box } from "@material-ui/core";
import { CreateGoalType } from "../../../../../utils/typeHelpers";
import { customShadows } from "../../../../../theme/shadows";

const ImageInput = () => {
  const { acceptedFiles, getRootProps, getInputProps } = useDropzone({
    maxFiles: 1,
  });

  const [image, setImage] = useState("");
  useEffect(() => {
    if (acceptedFiles.length > 0) {
      setImage(URL.createObjectURL(acceptedFiles[0]));
    }
  }, [acceptedFiles]);
  return (
    <div {...getRootProps({ className: "dropzone" })}>
      <input {...getInputProps()} />
      <BoxImage>
        {image === "" ? (
          <InfoText>Drag or Browse image</InfoText>
        ) : (
          <ProdImage src={image} alt="prodform" />
        )}
      </BoxImage>
    </div>
  );
};

export default ImageInput;

const BoxImage = styled(Box)({
  backgroundColor: "white",
  width: 160,
  height: 160,
  borderRadius: 11,
  justifyContent: "center",
  alignItems: "center",
  display: "flex",
  boxShadow: customShadows.z12,
});
const InfoText = styled(Typography)({
  color: "#3366FF",
  fontSize: 12,
  fontWeight: "bold",
});
const ProdImage = styled("img")({
  height: 160,
  borderRadius: 11,
});
