import * as yup from "yup";

const FILE_SIZE = 5 * 1000000;
const VIDEO_SIZE = 100 * 1000000;
const SUPPORTED_FORMATS = [
  "image/jpg",
  "image/jpeg",
  "image/gif",
  "image/png",
  "image/PNG",
  "image/webp",
  "image/svg",
];

const SUPPORTED_FORMATS_VIDEO = ["video/mp4", "video/mov"];
export const validationSchemaEditGoal = yup.object().shape({
  title: yup
    .string()

    .min(10, "Too Short, 10 character minimum")
    .max(25, '"Too long, 25 character max"'),
  short_description: yup
    .string()
    .min(10, "too Short, 30 character minimum")
    .max(100, "oo long, 150 character max"),

  buying_item: yup.string().min(10, "Too short 10 character minimum"),
  online_source_url: yup
    .string()
    .matches(
      /^(?:(?:(?:https?|ftp):)?\/\/)(?:\S+(?::\S*)?@)?(?:(?!(?:10|127)(?:\.\d{1,3}){3})(?!(?:169\.254|192\.168)(?:\.\d{1,3}){2})(?!172\.(?:1[6-9]|2\d|3[0-1])(?:\.\d{1,3}){2})(?:[1-9]\d?|1\d\d|2[01]\d|22[0-3])(?:\.(?:1?\d{1,2}|2[0-4]\d|25[0-5])){2}(?:\.(?:[1-9]\d?|1\d\d|2[0-4]\d|25[0-4]))|(?:(?:[a-z\u00a1-\uffff0-9]-*)*[a-z\u00a1-\uffff0-9]+)(?:\.(?:[a-z\u00a1-\uffff0-9]-*)*[a-z\u00a1-\uffff0-9]+)*(?:\.(?:[a-z\u00a1-\uffff]{2,})))(?::\d{2,5})?(?:[/?#]\S*)?$/,
      "Enter correct url!"
    ),

  media: yup
    .array(
      yup
        .mixed()
        .test(
          "fileSize",
          "Video too large,less than 100 MB",
          (value) => value && value.size <= VIDEO_SIZE
        )
        .test(
          "fileFormat",
          "Unsupported Format. mp4 and mov supported",
          (value) => value && SUPPORTED_FORMATS_VIDEO.includes(value.type)
        )
    )
    .nullable(),
  full_description: yup.string(),
});
